.content {
  width: 100%;
  max-width: 870px;
  margin: 0 0 0 48px;

  h1 {
    @include title-h1;
  }

  & > p {
    margin: 32px 0 0;
  }

  &__list {
    @include list;
    margin: 40px 0 0;

    &--service {
      display: flex;
      justify-content: space-between;

      .content__item {
        display: block;
        text-align: center;
        width: 49%;
        margin: 0;

        a {
          text-decoration: none;
          color: $black;

          &:hover {
            color: $blue;
          }
        }

        img {
          width: 100%;
          height: auto;
        }

        h2 {
          @include title-h2;
          margin: 16px 0 0;
        }
      }
    }
  }

  &__item {
    display: flex;
    margin: 0 0 32px;

    img {
      width: 180px;
      height: 195px;
    }
  }

  &__item-description {
    box-sizing: border-box;
    padding: 20px 20px 20px 40px;
    background-color: $grey-background;

    h2 {
      font-size: 1.2em;
      margin: 0;
    }

    p {
      margin: 16px 0 0;
    }
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    margin: 0 0 0 32px;

    & > p {
      margin: 24px 0 0;
    }

    &__list {
      margin: 32px 0 0;

      &--service {
        flex-direction: column;

        .content__item {
          width: 100%;
          max-width: 579px;
          margin: 0 auto 32px;

          h2 {
            @include title-h2;
            margin: 8px 0 0;
          }
        }
      }
    }

    &__item {
      display: flex;
      margin: 0 0 24px;
    }

    &__item-description {
      box-sizing: border-box;
      padding: 20px;
      background-color: $grey-background;

      h2 {
        font-size: 1em;
        margin: 0;
      }

      p {
        margin: 8px 0 0;
        font-size: 0.88em;
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    margin: 0;

    &__list {
      margin: 32px 0 0;

      &--service {
        flex-direction: column;

        .content__item {
          margin: 0 auto 24px;

          img {
            display: block;
          }
        }
      }
    }

    &__item {
      img {
        display: none;
      }
    }
  }
}
